.Spinner {
    display: inline-block;
    border: 2px solid #888;
    border-right-color: transparent!important;
    border-radius: 50%;
    animation: rotate 1s infinite linear;
}

@keyframes rotate {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
